<template>
    <div class="searchInfo" v-if="searchContent">
        <div>
            <div class="title">
                <p>{{searchContent}}</p>
            </div>
            <div class="info">
                <p></p>
                <p> {{searchTarget.length}} {{setString}}</p>
            </div>
        </div>
    </div>
</template>

<script>
import { computed } from '@vue/reactivity';
import { useRoute } from 'vue-router'

export default {
    props:['searchContent','searchTarget'],
    setup() {
        const route = useRoute();

        const setString = computed(()=>{
            return route.name == 'design'? '': ''
        })

        return{
            setString
        }
    },
}
</script>

